<template>
  <v-app>
    <v-app-bar app color="primary" dark>
      <v-toolbar-title>Horário</v-toolbar-title>
      <v-spacer />
      <v-btn text rounded @click="toggleDarkMode">
        <v-icon>mdi-theme-light-dark</v-icon>
      </v-btn>
      <v-btn
        v-for="link in links"
        :key="link.text"
        text
        rounded
        @click="checkForChangesBeforeNavigation(link.route)"
        >{{ link.text }}</v-btn
      >
      <v-btn
        v-if="this.$store.state.authToken"
        text
        rounded
        @click="checkForChangesBeforeLogout"
        >Sair</v-btn
      >
    </v-app-bar>

    <v-main>
      <router-view
        ref="current-component"
        @confirm-navigation="confirm(prentedRoute)"
      />
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "App",

  data: () => ({
    links: [{ text: "Configurações", route: "/listaConfiguracoes" }],
    prentedRoute: null,
    logoutConfirmationMessage:
      "Você tem alterações não salvas, que serão perdidas. Tem certeza que deseja sair?",
    navigationConfimationMessage:
      "Você tem alterações não salvas, que serão perdidas. Tem certeza que deseja sair dessa tela?",
  }),

  mounted() {
    this.checkLocalStorage();
  },

  methods: {
    checkForChangesBeforeLogout() {
      const stepsComponentOpen =
        this.$route.name === "Passos" && this.$refs["current-component"];
      const hasUnsavedChanges =
        stepsComponentOpen && !this.$refs["current-component"].handlePageExit();

      if (hasUnsavedChanges) {
        this.$store.dispatch(
          "setConfirmationDialogMessage",
          this.logoutConfirmationMessage
        );
        this.prentedRoute = "logout";
      } else {
        this.prentedRoute = null;
        this.logout();
      }
    },
    checkForChangesBeforeNavigation(route) {
      const stepsComponentOpen =
        this.$route.name === "Passos" && this.$refs["current-component"];
      const hasUnsavedChanges =
        stepsComponentOpen && !this.$refs["current-component"].handlePageExit();

      if (hasUnsavedChanges) {
        this.$store.dispatch(
          "setConfirmationDialogMessage",
          this.navigationConfimationMessage
        );
        this.prentedRoute = route;
      } else {
        this.prentedRoute = null;
        this.navigate(route);
      }
    },
    confirm(payload) {
      const stepsComponentOpen =
        this.$route.name === "Passos" && this.$refs["current-component"];
      if (stepsComponentOpen) {
        this.$refs["current-component"].beforeChangeStep();
      }

      this.prentedRoute = null;
      if (payload === "logout") {
        return this.logout();
      }
      this.navigate(payload);
    },
    navigate(route) {
      this.$router.push(route);
      this.prentedRoute = null;
    },
    checkLocalStorage() {
      const token = localStorage.getItem("authToken");
      if (token != null && token.length > 0) {
        this.$store.dispatch("setAuthToken", token);
        this.$router.push({ name: "ListaConfiguracoes" });
      }
      const darkMode = Number(localStorage.getItem("dark"));
      if (darkMode != null) {
        this.$vuetify.theme.dark = Boolean(darkMode);
      } else {
        this.$vuetify.theme.dark = false;
      }
    },
    logout() {
      this.$store.dispatch("clearAuthToken");
      this.$router.push({ name: "Autenticacao" });
    },
    toggleDarkMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      localStorage.setItem("dark", Number(this.$vuetify.theme.dark));
    },
  },
};
</script>

<style>
.v-card__text,
.v-card__title {
  word-break: normal; /* maybe !important  */
}
</style>
